<template>
<div class="askdetails">
  <div class="d-main">
    <div v-for="(firstValue,firstIndex) in responseList" :key="firstIndex">
      <div class="d-main-list" v-for="(secondValue,secondIndex) in firstValue.qmsj_question_list" :key="secondIndex" v-if="secondValue.format_answer.length>0">
        <div class="title">{{secondValue.question_name}}</div>
        <div class="answer">{{secondValue.format_answer.join(',')}}</div>
      </div>
    </div>
    <div class="pic clear">
      <ul>
        <li v-for="(pic,index) in picList" :key="index"><img :src="pic.attachment" preview="1"></li>
      </ul>
    </div>
    <div class="comment">
      <div v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" :infinite-scroll-immediate-check="true" infinite-scroll-distance="10">
        <div class="com-user" v-for="(v,i) in newsListData" :key="i">
          <div class="u-top">
            <div class="name">{{v.qmsj_people_name}}</div>
            <div class="time">{{v.format_add_time}}</div>
          </div>
          <div class="u-main">{{v.comment}}</div>
        </div>
      </div>
    </div>
    <div class="loading" v-if="isLoading">
      <div class="lcing">
        <mt-spinner class="cir" type="fading-circle" :size="30"></mt-spinner> 加载中
      </div>
    </div>
  </div>
  <div class="commentarea" v-if="details.can_comment">
    <div class="pltext"><input @blur="blurComment" class="wftext" type="text" v-model="answerComment" placeholder="请输入您的评论"></div>
      <div class="plbtn">
        <mt-button size="small" type="primary" @click="submitComment">提交</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  Toast
} from 'mint-ui';
import {
  getReadRespondentDetails,
  uploadComment,
  getReadRespondentCommentList
} from '@/api/http'
import {
  setTimeout
} from 'timers';
export default {
  data() {
    return {
      newsListData: [],
      hasHistory: false, // 是否有记录
      loading: true, //为true时不会触发加载更多，false可以触发
      noMore: false, // 是否还有更多
      isLoading: false, // 加载中转菊花
      details: '',
      responseList: [],
      picList: [],
      answerComment: '',
      responseID: '',
      commentValue: {
        page: 1,
        count: 20,
        qmsj_respondent_id: ''
      }
    }
  },
  mounted() {
    let self = this;
    //异步插入的图片
    setTimeout(() => {
      self.$previewRefresh()
    }, 2000);
    //图片查看器打开后，打印本次查看器的实例（事件、方法、属性的示例）
    this.$preview.on('imageLoadComplete', (e, item) => {
      // console.log(this.$preview.self)
    })
  },
  methods: {
    async getReadRespondentDetails() {
      const res = await getReadRespondentDetails({
        qmsj_respondent_id: this.responseID
      })
      this.details = res.qmsj_respondent_info
      this.responseList = res.qmsj_respondent_info.qmsj_respondent_module_list
      this.picList = res.qmsj_respondent_info.qmsj_respondent_attachment_list
    },
    async getCommentList() {
      this.commentValue.qmsj_respondent_id = this.responseID
      const res = await getReadRespondentCommentList(this.commentValue)
      let resnewsListLength = res.qmsj_respondent_comment_list.length;
      this.isLoading = false;
      if (resnewsListLength == 0 && this.newsListData.length == 0) {
        this.loading = true;
        this.hasHistory = true
      } else {
        this.hasHistory = false
        //有数据并且请求到的数据等于pageSize说明数据库还有多余数据
        if (resnewsListLength === parseInt(this.commentValue.count)) {
          for (var i = 0; i < this.commentValue.count; i++) {
            this.newsListData.push(res.qmsj_respondent_comment_list[i]);
          }
          this.loading = false;
        } else {
          //有数据但是数据库数据已经被请求完，没有其他数据
          this.loading = true;
          this.noMore = true;
          for (var i = 0; i < resnewsListLength; i++) {
            this.newsListData.push(res.qmsj_respondent_comment_list[i]);
          }
        };
        //数据正好请求完，由于前面加了resnewsListLength == 0限制，会走当前逻辑
        if (resnewsListLength == 0) {
          this.loading = true;
          this.noMore = true;
        }
      }
    },
    loadMore() {
      this.commentValue.page++;
      this.loading = true;
      this.isLoading = true;
      this.getCommentList();
    },
    async submitComment() {
      let data = {}
      data.qmsj_respondent_id = this.responseID
      data.comment = this.answerComment
      if (this.answerComment == '') {
        Toast('请填写评论后提交');
        return
      }
      const res = await uploadComment(data)
      Toast('提交成功');
      this.answerComment = ''
      this.newsListData = []
      this.commentValue.page = 1
      this.getCommentList()
    },
    blurComment(){
      window.scroll(0,0)
    }
  },
  watch: {
    '$route': {
      immediate: true, // 确认是否以当前的初始值执行handler的函数
      handler() {
        this.responseID = this.$route.params.id
        this.getReadRespondentDetails();
        this.getCommentList()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.askdetails {
  padding: 10px;
  background-color: #fff;

  .bigpic {
    width: 280px;
  }

  .d-main {
    padding-bottom: 50px;

    .pic {
      margin-bottom: 20px;

      li {
        width: 60px;
        height: 60px;
        float: left;
        margin-right: 10px;

        img {
          width: 60px;
          height: 60px;
          object-fit: cover;
        }
      }
    }

    .d-main-list {
      margin-bottom: 10px;
      padding-bottom: 10px;
      border-bottom: 1px solid #dcdfe6;

      .title {
        font-size: 14px;
      }

      .answer {
        font-size: 16px;
        color: #388ff9;
      }
    }

    .comment {
      .com-user {
        font-size: 14px;
        margin-bottom: 20px;

        .u-top {
          display: flex;
          height: 30px;

          >div {
            flex: 1;
            width: 0;
          }

          .name {
            font-size: 16px;
            font-weight: bold;
          }

          .time {
            text-align: right;
            color: rgb(158, 158, 158);
          }
        }
      }

    }

    .commit {
      font-size: 0;
      text-align: center;
      padding-bottom: 10px;
    }

    .loading {
      padding: 20px 0;

      .lcing {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14px;

        .cir {
          margin-right: 8px;
        }
      }

    }

  }

  .commentarea {
    height: 50px;
    position: fixed;
    z-index: 10;
    bottom: 0;
    left: 0;
    width: 375px;
    background: #d8d8d8;
    display: flex;
    align-items: center;
    font-size: 0;

    .pltext {
      flex: 0 0 290px;
      margin: 0 10px;

      .wftext {
        width: 100%;
        height: 30px;
        padding: 0 10px;
      }
    }

    .plbtn {
      flex: 1;
    }

  }
}
</style>



